#tooltip {
  color: white;
  position: absolute;
  text-align: center;
  padding: 4px;
  font-size: 1.2rem;
  background: rgba(0, 0, 0, .8);
  border: 2px solid black;
  border-radius: 2px;
  pointer-events: none;
}
.rc-slider-mark-text {
  font-size: 1rem;
}

.rc-slider-mark-text-active {
  color: #FF8A65;
  font-size: 1.2rem;
}

.rc-slider-rail, .rc-slider-step {
  background-color: #cccccc;
}

.rc-slider-handle {
  height: 16px;
  width: 16px;
  margin-top: -6px;
  background-color: #FF8A65 !important;
}
/* ========== GRAPH ========== */

#graph .countries:hover .country {
  opacity: .04 !important;
}

#graph .countries .country:hover {
  opacity: 1 !important;
}

#graph .dots, #graph .line {
  stroke-width: 6;
}

#graph .line {
  fill: none;
  opacity: 0.8;
}

#graph .line:hover {
  opacity: 1;
}

#graph .dots {
  stroke-width: 4;
}

#graph .selected {
  visibility: visible !important;
}

#graph .metrics:hover .metric {
  opacity: 0.1;
}

#graph .metrics .metric:hover {
  opacity: 1;
}

/* ========== MAP ========== */

#map .countries {
  fill: black;
}

#map .countries path {
  stroke: transparent;
  opacity: 0.9;
  stroke-width: 0.4;
  stroke-opacity: 1;
}

#map .countries .selected {
  opacity: 1;
}

#map .countries path:hover {
  stroke-width: 1.2;
  opacity: 1;
}

#map .names {
  fill: none;
  stroke: white;
  stroke-linejoin: round;
}

#map .names:hover {
  fill: black;
}

/* ========== CHORD ========== */

#chord .arcs {
  fill-opacity: 0.8;
}

#chord path.fade {
  fill-opacity: .1;
  stroke-opacity: .1;
}

#chord path.show {
  fill-opacity: 1;
  stroke-opacity: 1;
}
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: "Roboto", "Cantarell", "Droid Sans", "Helvetica Neue", sans-serif;
  color: white;
  background-color: #212121;
  /* background-color: #424242; */
}

main, nav {
  margin: 0 1rem;
}

nav {
  padding: 0.6rem 0;
  height: 5vh;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.row {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 46vh;
}

.row>div {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin: 0;
  border: 1px dashed #888;
}

.control {
  border: none !important;
}

.control:nth-child(1) {
  width: 35%;
  padding-right: 1rem;
}

.control:nth-child(2) {
  width: 15%;
  padding-right: 1rem;
}

.control:nth-child(3) {
  width: 45%;
  overflow: hidden;
}

.control:nth-child(4) {
  text-align: center;
  width: 5%;
}

.idiom {
  box-sizing: border-box;
  padding: 0.4rem;
  height: 100%;
}

.idiom>div {
  width: 100%;
  height: 100%;
}

.idiom:nth-child(1) {
  width: 30%;
}

.idiom:nth-child(2) {
  width: 70%;
}
